@import 'base/mixins';
@import 'fonts.scss';


body {

    background: #FAFCFF;
}

html,
body {
    overflow-x: hidden;
}

.container {
    position: relative;

    max-width: 1262px;
    width: 100%;
    margin: 0 auto;

    @include laptop {
        max-width: 960px
    }

    @include tablet {
        max-width: 720px;
    }

    @include tablet-sm {
        max-width: 540px;
    }

    @include phone {
        max-width: 100%;
        padding: 0 22px;
    }
}

.logo {
    display: block;

    width: max-content;
    margin: 0 auto;

    &__img {
        display: block;

        width: 306px;

        @include phone {
            width: 213px;
        }
    }
}

.title-main {
    font-family: 'RFDewiExtended';
    font-style: normal;
    font-weight: 700;
    font-size: 52px;
    line-height: 107%;
    text-align: center;
    letter-spacing: -0.01em;
    color: #344463;

    @include laptop {
        font-size: 48px;
    }

    @include tablet {
        font-size: 44px
    }

    @include phone {
        font-size: 35px;
    }

    .colored {
        background: linear-gradient(266.92deg, #6395F8 6.31%, #B68FFF 91.68%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }
}

.btn {
    padding: 21px 145px;
    border: none;
    background: linear-gradient(266.92deg, #7CAEF5 6.31%, #BBB8F7 91.68%);
    border-radius: 25px;

    font-family: 'RFDewiExtended';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 143.84%;
    color: #15172A;

    outline: none;
    cursor: pointer;

    box-shadow: 0 20px 34px rgba(0, 0, 0, .23);

    transition: cubic-bezier(0.075, 0.82, 0.165, 1) .3s all;

    @include tablet {
        font-size: 16px;
        padding: 17px 130px;
        border-radius: 21px;
    }

    @include phone {
        width: 100%;
        font-size: 16px;
        padding: 16px 16px;
        border-radius: 21px;
    }

    &:hover {
        background: linear-gradient(266.92deg, #6395F8 6.31%, #B68FFF 91.68%), linear-gradient(266.92deg, #7CAEF5 6.31%, #BBB8F7 91.68%);
    }
}

br.mobile {
    display: none;

    @include phone {
        display: inline-block
    }
}

.hero {
    padding-top: 20px;
    padding-bottom: 40px;

    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;

        padding-top: 48px;

        @include phone {
            padding-top: 33px;
        }
    }

    &__title {
        max-width: 700px;
        margin-bottom: 24px;
    }

    &__text {
        max-width: 576px;
        margin-bottom: 48px;

        font-family: 'RFDewiExtended';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 135%;
        text-align: center;
        color: #344463;

        @include phone {
            margin-bottom: 24px;
            font-size: 18px;
        }
    }
}

.now {

    padding-top: 55px;
    padding-bottom: 20px;

    &__list {
        display: flex;
        justify-content: center;
        position: relative;
    }

    &__item {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 46px;
        overflow: hidden;
        box-shadow: 0 20px 34px rgba(0, 0, 0, .23);

        max-width: 660px;

        &::before {
            z-index: 2;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(40, 45, 73, 0.7);
        }

        &:nth-of-type(1) {
            z-index: 3;
            top: 0;
            left: 0;
            position: relative;
            transform: none;

            &::before {
                // background: linear-gradient(180deg, rgba(40, 45, 73, 0) 0%, rgba(40, 45, 73, 0.2706) 31.6%, rgba(40, 45, 73, 0.82) 100%);
                background: linear-gradient(180deg, rgba(40, 45, 73, 0) 0%, rgba(40, 45, 73, 0.2706) 31.6%, rgba(40, 45, 73, 0.82) 100%)
            }

            .now__item-bg {
                filter: none
            }

            &:hover {
                .now__item-icon {
                    transform: scale(1.1);
                }
            }
        }

        &:nth-of-type(2),
        &:nth-of-type(3) {
            z-index: 2;
            min-width: 540px;
        }

        &:nth-of-type(2) {
            margin-left: -325px;
        }

        &:nth-of-type(3) {
            margin-left: 325px;
        }

        &:nth-of-type(4),
        &:nth-of-type(5) {
            z-index: 1;
            min-width: 452px;

            .now__item-title {
                font-size: 20px;
            }
        }

        &:nth-of-type(4) {
            margin-left: -540px;
        }

        &:nth-of-type(5) {
            margin-left: 540px;
        }

        &-inner {
            z-index: 5;
            position: absolute;
            top: 50%;
            left: 50%;
            color: #fff;
            transform: translate(-50%, -50%);
        }

        &-play {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            cursor: pointer;
        }

        &-icon {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 11px;
            transition: .3s;

            &.--lock {
                width: 56px;
                height: 56px;
                border: 1.5px solid rgba(#E2E8F1, 0.5);
            }

            &.--play {
                width: 69px;
                height: 69px;
                padding-left: 3px;
                background: linear-gradient(266.92deg, #6395F8 6.31%, #B68FFF 91.68%);
            }
        }

        &-desc {
            font-family: 'RFDewiExtended';
            font-size: 15px;
            line-height: 1.3;
            text-align: center;

            &.--bold {
                font-weight: 700;
                font-size: 16px;
                line-height: 111.2%;
            }
        }

        &-title {
            z-index: 6;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 45px;
            padding-left: 10px;
            padding-right: 10px;
            font-family: 'RFDewiExtended';
            font-style: normal;
            font-weight: 700;
            font-size: 25px;
            line-height: 111.2%;
            letter-spacing: -0.02em;
            color: #F4F7FE;
            width: 100%;
            text-align: center;

        }

        &-bg {
            filter: blur(10px);
            transform: scale(1.1);

            img {
                z-index: 1;
                position: relative;

                width: 100%;
            }
        }
    }
}


@media screen and (max-width: 1300px) {
    .now {
        &__item {
            &:nth-of-type(2) {
                margin-left: -150px;
            }

            &:nth-of-type(3) {
                margin-left: 150px;
            }

            &:nth-of-type(4) {
                margin-left: -250px;
            }

            &:nth-of-type(5) {
                margin-left: 250px;
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .now {
        &__left {
            max-width: 415px;
        }

        &__right {
            padding-right: 0;
            max-width: 400px;
        }
    }
}


@media screen and (max-width: 992px) {
    .now {
        &__left {
            max-width: 100%;
            margin-bottom: 24px;
        }

        &__right {
            max-width: 100%;
        }

        &__btn {
            margin-right: 0;
        }

    }
}

@media screen and (max-width: 767px) {
    .now {
        padding-left: 22px;
        padding-right: 22px;

        &__inner {
            padding: 56px 22px 90px;
            margin-left: -22px;
            margin-right: -22px;
        }

        &__title {
            font-size: 20px;
            line-height: 24px;
        }

        &__text {
            font-size: 16px;
            line-height: 24px;

            p {
                margin-bottom: 24px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .container {
            padding-left: 0;
            padding-right: 0;
            max-width: 100%;
        }

        &__list {
            padding-left: 28px;
            padding-right: 28px;
            margin-top: -40px;
        }

        &__item {
            transform: none;
            min-width: 1px !important;

            &:nth-of-type(1) {
                min-height: 315px;

                .now__item-bg {
                    width: 100%;
                    height: 100%;
                    transform: none;
                }
            }

            &:nth-of-type(2) {
                z-index: 2;
                top: 25px;
                bottom: 25px;
                left: -8px;
                right: -8px;
                margin-left: 0;
            }

            &:nth-of-type(3) {
                z-index: 1;
                top: 44px;
                bottom: 44px;
                left: -32px;
                right: -32px;
                margin-left: 0;
            }

            &:nth-of-type(4),
            &:nth-of-type(5) {
                display: none;
            }

            &-title {
                display: none;
            }

            &-icon {
                &.--play {
                    padding: 17px;
                    width: 51px;
                    height: 51px;

                    svg {
                        position: relative;
                        left: 2px;
                    }
                }
            }

            &-desc {
                &.--bold {
                    font-size: 14px;
                    line-height: 15px;
                }
            }

            &-bg {
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

.pluses {

    position: relative;

    &__grid {
        position: relative;
        z-index: 2;

        padding-top: 309px;
        padding-bottom: 281px;
        max-width: 543px;

        @include laptop {
            padding-top: 280px;
            padding-bottom: 260px; 
        }

        @include tablet {
            padding-top: 90px;
            padding-bottom: 450px;
        }

        @include phone {
            padding-top: 76px;
            padding-bottom: 417px;
        }
    }

    &__text {
        font-family: 'RFDewiExtended';
        font-style: normal;
        font-weight: 600;
        font-size: 25px;
        line-height: 143.84%;
        color: #344463;

        @include laptop {
            font-size: 22px;
        }

        @include tablet {
            text-align: center;
            font-size: 24px;
        }

        @include phone {
            font-size: 18px;
            text-align: center;
            font-weight: 700;
        }

        br {
            @include phone {
                display: none;
            }
        }
    }

    .container {
        @include phone {
            overflow: hidden
        }
    }

    &__img {
        position: absolute;
        z-index: 2;

        top: 172px;
        right: -144px;

        @include laptop {
            width: 600px;
        }

        @include tablet {
            top: unset;
            bottom: 15px;
            left: 50%;
            right: unset;

            width: 500px;
            height: 367px; 

            transform: translateX(-50%);
        }

        @include phone {
            top: unset;
            bottom: 15px;
            left: -29px;
            right: unset;

            width: 551px;
            height: 367px;

            transform: none;
        }
    }

    &__bg {
        position: absolute;
        z-index: 1;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;

        display: block;

        width: 100%;
        height: 100%;

        @include phone {
            top: unset;
            height: 500px;
        }
    }
}

.why {
    padding: 104px 0 142px;
    position: relative;

    @include laptop {
        padding: 80px 0 125px;
    }

    @include tablet {
        padding: 70px 0 100px;
    }

    @include phone {
        padding: 60px 0 99px;
    }

    &:before {
        content: '';

        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 1;

        display: block;

        width: 50%;
        height: 100%;

        background: #F4F7FE;

    }

    .container {
        z-index: 2;
    }

    &__title {
        margin-bottom: 44px;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: 376px;
        gap: 22px;

        @include laptop {
            grid-auto-rows: 350px;
        }

        @include tablet {
            grid-template-columns: repeat(2, 1fr);
            grid-auto-rows: 350px;
        }

        @include phone {
            grid-template-columns: 100%;
            grid-auto-rows: 307px;
            gap: 20px;
        }
    }

    &__item {
        position: relative;
        z-index: 1;

        @include phone {
            max-width: 330px;
            margin: 0 auto;
        }

        &:after {
            content: '';

            position: absolute;
            left: 15px;
            right: 15px;
            bottom: 0px;
            z-index: -1;

            display: block;
            // width: 100%;
            height: 20px;
            border-radius: 25px;

            box-shadow: 0 10px 19px rgba(0, 0, 0, .10);
        }

        &-bg {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            position: relative;
            z-index: 2;

            height: 100%;
            padding: 44px 32px 33px;
            background: #FFFFFF;
            border-radius: 25px;
            // box-shadow: 0 10px 19px rgba(0, 0, 0, .10);

            @include tablet {
                padding: 35px 25px 25px;
            }
    
            @include phone {
                padding: 44px 30px 40px;
            }
        }

        &-number {
            font-family: 'RFDewiExtended';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 143.84%;
            background: linear-gradient(266.92deg, #6395F8 6.31%, #B68FFF 91.68%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;

            width: max-content;
        }

        &-body {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }

        &-title {
            margin-bottom: 16px;
            font-family: 'RFDewiExtended';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 120%;
            color: #344463;
        }

        &-text {
            min-height: 88px;
            margin: 0;

            font-family: 'SFProText';
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 143.84%;
            color: #344463;

            @include phone {
                min-height: 80px;
                font-size: 14px;
            }
        }
    }
}

.offer {
    padding: 116px 0 123px;

    @include laptop {
        padding: 100px 0 115px;
    }

    @include tablet {
        padding: 85px 0 115px;
    }

    @include phone {
        padding: 75px 0 115px;
    }

    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        margin-bottom: 32px;

        @include phone {
            margin-bottom: 24px;   
        }
    }

    &__text {
        margin-bottom: 32px;

        font-family: 'RFDewiExtended';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 135%;
        text-align: center;
        color: #344463;

        @include phone {
            font-size: 18px;   
        }
    }
}